html,
body {
  width: 100vw;
  height: 100vh;
  background-color: black;
  position: fixed;
}

.app {
  background-color: black;
  width: 100vw;
  height: 75vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding-top: 20vh;
}

.clover {
  height: 25vh;
}

.scooter {
  position: absolute;
  bottom: -1vh;
  left: 0;
  width: 10vw;

  -webkit-animation: sliding 4s infinite; /* Chrome, Safari, Opera */
  -webkit-animation-direction: normal; /* Chrome, Safari, Opera */
  animation: sliding 4s infinite;
  animation-direction: normal;
}

.company-name {
  font-family: "Fira Sans", sans-serif;
  font-size: 10vh;
  color: white;
  padding-top: 7vh;
  width: 100vw;
  text-align: center;
}

.cooming-soon {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 5vh;
  color: white;
  padding-top: 15vh;
  width: 100vw;
  text-align: center;
}

@-webkit-keyframes sliding {
  0% {
    left: -11vw;
  }
  25% {
    left: 5vw;
  }
  100% {
    left: 111vw;
  }
}

@keyframes sliding {
  0% {
    left: -11vw;
  }
  25% {
    left: 10vw;
  }
  100% {
    left: 111vw;
  }
}



